<template>
    <!-- NAVBAR -->
    <nav>
      <button v-if="currentView=='schedule'" class="back" @click="toggleCurrentView">
          <ion-icon name="arrow-back-outline"></ion-icon>
      </button>
      <button v-if="currentView=='folders'" class="addFolder" @click="toggleNewFolderPrompt">
          <ion-icon name="add-outline"></ion-icon>
      </button>
      {{navbarTitle}}
    </nav>
    <!-- NEW FOLDER PROMPT AND OVERLAY -->
    <div v-if="visible" class="overlay" @click.self="toggleNewFolderPrompt">
        <form @submit.prevent="addNewFolder(newFolderName)" class="prompt">
            <p>New folder</p>
            <input type="text" placeholder="Name" v-model="newFolderName">
            <button>Confirm</button>
        </form>
    </div>
    <!-- VIEW FOLDERS -->
    <div class="foldersView" v-if="currentView=='folders'">
          <p v-if="folders.length==0" class="noFoldersMsg">
          No folders yet. <br> <br>
          Tap 
          <span class="plusSample">
              <ion-icon name="add-outline"></ion-icon>
          </span> 
          to add new folder.
      </p>
      <div v-else v-for="folder in folders" :key="folder.id">
          <div class="folder" @click.self="openFolder(folder.id)">
              <img @click="openFolder(folder.id)" src="./folder.webp">
              <p class="name" @click="openFolder(folder.id)">{{folder.name}} <span class="number">({{folder.notes.length}})</span></p>
              <button @click="removeFolder(folder.id)">
                  <ion-icon name="close-outline"></ion-icon>
              </button>
          </div>
      </div>
    </div>

  <!-- VIEW SCHEDULE -->
  <div class="viewSchedule" v-if="currentView=='schedule'">
      <div class="folder">
        <!-- create new note -->
        <form @submit.prevent="createNote(newNoteText), clearInput()">
            <input type="text" v-model="newNoteText" placeholder="New task">
            <button>
                <ion-icon name="add-outline"></ion-icon>
            </button>
        </form>
        <!-- render existing notes -->
        <div 
          v-for="note in folders[currentFolder].notes" 
          class="note" :class="{complete: note.complete}" 
          @click.self="toggleCompleteNote(note.id)"
          :key="note.id">
            {{note.text}}
            <button class="removeNote" @click="removeNote(note.id)">
                <ion-icon name="close-outline"></ion-icon>
            </button>
        </div>
    </div>
  </div>

</template>

<script setup>
  import { ref } from 'vue'

  let folderId = 0
  let noteId = 0
  let currentView = ref('folders')
  const currentFolder = ref(0)
  const folders = ref([
    {id: folderId++, name: 'Sample Folder', notes: [
      {id: noteId++, text: 'Sample Note Title', complete: false},
      {id: noteId++, text: 'Another sample title', complete: false}
    ]}
  ])

  const navbarTitle = ref('Folders')

  const newNoteText = ref('')
  const clearInput = () => {
      newNoteText.value = ''
  }


  let visible = ref(false)
  const toggleNewFolderPrompt = () => {
    visible.value = !visible.value
  }
  const addNewFolder = (name) => {
    folders.value.push({id: folderId++, name: name, notes: []})
    toggleNewFolderPrompt()
  }
  const removeFolder = (folderId) => {
    folders.value = folders.value.filter((folder)=> folder.id !== folderId)
  }
  const openFolder = (folderId) => {
    for(let i=0; i<folders.value.length; i++){
      if(folders.value[i].id == folderId){
        currentFolder.value = i
      }
    }
    toggleCurrentView()
  }
  const toggleCurrentView = () => {
    if(currentView.value == 'schedule')
    {
      currentView.value = 'folders'
      navbarTitle.value = 'Folders'
    }
    else{
      currentView.value = 'schedule'
      navbarTitle.value = folders.value[currentFolder.value].name
    }
  }
  const createNote = (text) => {
    folders.value[currentFolder.value].notes.push({id: noteId++, text: text, complete: false})
  }

  const removeNote = (noteId) => {
    folders.value[currentFolder.value].notes = folders.value[currentFolder.value].notes.filter((note)=> note.id !== noteId)
  }

  const toggleCompleteNote = (noteId) => {
    for(let i=0; i<folders.value[currentFolder.value].notes.length; i++){
      if(folders.value[currentFolder.value].notes[i].id == noteId){
        noteId = i
      }
    }
    if(folders.value[currentFolder.value].notes[noteId].complete){
      folders.value[currentFolder.value].notes[noteId].complete = false
    }
    else{
      folders.value[currentFolder.value].notes[noteId].complete = true
    }
    
  }

</script>

